import React, { useState } from "react";
import { Card, Form } from "react-bootstrap";
import SentimentChart from "../../analytics-new/layout/common/graph/sentimentChart";
import { reArrangeSeriesSentiment } from "../../../../utils";
import { getsentimentLiveTrendMore } from "../../../../actions/display/dashboardActions";
import { useDispatch } from "react-redux";
import moment from "moment";

    const LiveTrend = (props) => {

        const [activeTime, setactiveTime] = useState(0);
        const dispatch = useDispatch()
                
        const getSentimentData=(value)=>{  
            setactiveTime(value)
            dispatch(getsentimentLiveTrendMore(value))
        }   


        const { live_trend, loader } = props

        console.log(props,'sentiment_live_loadersentiment_live_loader')

        const getinitiallastScore =  live_trend.initiallastScore && Object.keys(live_trend.initiallastScore.length) ? live_trend.initiallastScore[0] : []
 
        if(getinitiallastScore && getinitiallastScore.length){
            live_trend.data[0] =   Number(getinitiallastScore.sentiment)
         }
      
         const sentimentAnalytics = live_trend && Object.keys(live_trend).length ? reArrangeSeriesSentiment([live_trend],1) : [];
         
     return (
       sentimentAnalytics && Object.keys(live_trend.initiallastScore).length || loader ?  
       <div className="col-lg-6">  
        <div className='created-walls-section mb-5'>
            <Card className='p-0 card-custom card-stretch'>
                <Card.Header className='p-5 mb-8'>
                    <div className='m-0 d-flex align-items-center justify-content-between card-title h5 w-100'>
                        <h6 class="card-label font-weight-bolder m-0 ">Sentiment Live Trend</h6>
                        <div className='text-secondary'>
                            {moment().format('hh:mm A MMMM D, YYYY')}
                        </div>
                    </div>
                </Card.Header>

                <div className="date-filter date-filter-2">
                <Form.Select value={activeTime} onChange={(e)=>getSentimentData(e.target.value)} size="md">
                    <option value={1}>Last 24 Hours</option>
                    <option value={2}>Last 48 Hours</option>
                </Form.Select>
                </div> 

                <SentimentChart key={loader} contentAnalyticsData={sentimentAnalytics} type={"line"} /> 
            </Card>
       </div>
      </div>
        : null 
    );
 };

 export default React.memo(LiveTrend)