
import React, { PureComponent } from "react";
import ReactApexChart from "react-apexcharts";
import ApexCharts from "apexcharts";
import moment from "moment";
export default class SentimentChart extends PureComponent {
  constructor(props) {
    super(props);

    const getData = this.props.contentAnalyticsData.newSeries && this.props.contentAnalyticsData.newSeries[0]['data']
    const lastCategory = getData && Object.keys(getData).length && getData.length-1; // Use dynamic category if needed
    const seriesData   = getData && Object.keys(getData).length && getData;
    const maxData = getData && Object.keys(getData).length && getData.some(value => value[1] > 0) ? 1 : 0;

    const uniqueData = getData && Object.keys(getData).length && getData.reduce((acc, item) => {
      let getItem = Number(item[1])
       if (!acc.includes(getItem)) {
        acc.push(getItem);
      }
      return acc;
    }, []);
    
   
    this.state = {
      series: this.props.contentAnalyticsData.newSeries,
      options: {
        colors: ["#546FED"],
        chart: {
          id: "area-datetime",
          toolbar: { show: false },
          type: "line",
          animations: {
            enabled: true,
            easing: "linear",
            dynamicAnimation: {
              speed: 1000,
            },
          },
          height: 350,
          zoom: { enabled: false },
        },
        legend: {
          show: true,
          position: "top",
          itemMargin: {
            horizontal: 20,
            vertical: 0,
          },
        },
        dataLabels: { enabled: false },
        markers: { 
        size: maxData && uniqueData.length>1 ? 4 : 0, style: "hollow" },
        stroke: {
          width: [maxData ? 4 : 0],
          curve: "straight",
        },
        xaxis: {
          type: "datetime", 
          min: this.props.contentAnalyticsData.unixDate && this.props.contentAnalyticsData?.unixDate[0],
          max: (this.props.contentAnalyticsData.unixDate && this.props.contentAnalyticsData?.unixDate[lastCategory]) + (2 * 60 * 60 * 1000), // Subtract 2 hours from the end time
          labels: {  
            formatter: (value) => {
              console.log(moment(value).format('HH:mm'),'valuevaluevalue')
              return moment(value).format('HH:mm')
            }
          },
          tooltip: {
            shared: true,
            intersect: false, // Allows tooltip to show when hovering near the points
            formatter: function (value) {
              return moment(value).format('DD-MM-YY HH:mm')
            },
            
          }
          ,tickPlacement: 'on', // Optional: places ticks directly on the x-axis labels

        },
        yaxis: {
          min: 0,   // Fixing the y-axis minimum value to 0
          max: 100, // Fixing the y-axis maximum value to 100
          labels: {
            formatter: function (value) {
              return Math.round(value); // Rounds the value to the nearest integer
            }
          }
        },
      
        annotations: {
          points: [
            {
              x: seriesData ? lastCategory && seriesData[lastCategory][0]-1 : 0, // Dynamically set based on last category
              y: seriesData ? lastCategory && seriesData[lastCategory][1] : 0, // Last data point
              marker: {
                size: maxData ? 10 : 0, // Highlight the last marker
                fillColor: "#546FED",
                strokeColor: "#2545d7",
                shape: "circle",
                cssClass: "blinking-marker", // Apply custom CSS class for blinking effect
              },
              label: {
                borderColor: "#546FED",
                offsetY: -10, // Move the label higher to avoid overlap
                offsetX: -10, // Move the label higher to avoid overlap
                style: {
                  color: "#546FED",
                  background: "transparent", // Make label background transparent
                  zIndex: 0, // Lower z-index to ensure marker is on top
                },
                //text: `${lastCategory && seriesData[lastCategory][1]}`,
              },
            }],
          yaxis: [
            {
              y: 30,
              y2: 0,
              borderColor: "transparent",
              fillColor: "#f7caca", // Color for 0-30 range
            },
            {
              y: 60,
              y2: 30,
              borderColor: "transparent",
              fillColor: "#f9debe", // Color for 30-60 range
            },
            {
              y: 100,
              y2: 60,
              borderColor: "transparent",
              fillColor: "#cfe7d5", // Color for 60-100 range
            },
          ],
        },
        tooltip: {
          enabled: true,
          x: { format: "dd MMM yyyy" },
          custom: ({ series, seriesIndex, dataPointIndex, w }) => {

             const seriesData = series[0][dataPointIndex]
              return (!maxData ? "" : 
                seriesData<=30 ?
                `<div class="apexcharts-tooltip-item bg-red"><img src="https://cloud.tagbox.com/react-app/media/icons/emoji/emoji5.svg">` +
                `<div><strong>Negative</strong><br/>` + `${seriesData}%` +`</div></div>`
                  : seriesData<=60 ? 
                `<div class="apexcharts-tooltip-item bg-yelow"><img src="https://cloud.tagbox.com/react-app/media/icons/emoji/emoji3.svg">` + 
                `<div><strong>Neutral</strong><br/>` + `${seriesData}%` + `</div></div>`
                  :
                `<div class="apexcharts-tooltip-item bg-green"><img src="https://cloud.tagbox.com/react-app/media/icons/emoji/emoji1.svg">` +
                `<div><strong>Positive</strong><br/>` + `${seriesData}%` + `</div></div>`
              )
          },
        },
        grid: {
          show: false, // Enable or disable the entire grid (optional)
        },
      },
      responsive: [
        {
          // breakpoint: 991,
          options: {
            chart: {
              height: 300,
            },
          },
          // breakpoint: 767,
          options: {
            chart: {
              height: 280,
            },
          },
        },
      ],
    };
  }

  render() {

    return (
     <> 
      <ReactApexChart
        options={this.state.options}
        series={this.state.series}
        type="line"
        height={350}
      />
           
     </> 
    );
  }
}
